/**
 * Retrieves the translation of text.
 *
 * @see https://developer.wordpress.org/block-editor/reference-guides/packages/packages-i18n/
 */
import { __ } from '@wordpress/i18n';

/**
 * React hook that is used to mark the block wrapper element.
 * It provides all the necessary props like the class name.
 *
 * @see https://developer.wordpress.org/block-editor/reference-guides/packages/packages-block-editor/#useblockprops
 */
import { useBlockProps } from '@wordpress/block-editor';

/**
 * Lets webpack process CSS, SASS or SCSS files referenced in JavaScript files.
 * Those files can contain any CSS code that gets applied to the editor.
 *
 * @see https://www.npmjs.com/package/@wordpress/scripts#using-css
 */
import './editor.scss';

/**
 * The edit function describes the structure of your block in the context of the
 * editor. This represents what the editor will render when the block is used.
 *
 * @see https://developer.wordpress.org/block-editor/reference-guides/block-api/block-edit-save/#edit
 *
 * @return {WPElement} Element to render.
 */

import { Fragment } from '@wordpress/element';
import { InspectorControls, BlockControls, AlignmentControl, PanelColorSettings } from '@wordpress/block-editor';
import { RangeControl, PanelBody, TextControl, ToggleControl } from '@wordpress/components';
import ServerSideRender from '@wordpress/server-side-render';


export default function Edit( props ) {
	const { setAttributes, attributes } = props;
	const { textAlign, displayInline, showTooltip, tooltipText, tooltipColor, tooltipBgColor, tooltipBorderColor, btnBackgroundColor, btnIconBackgroundColor, iconColor, btnTextColor, btnBorderRadius, btnBordercolor } = attributes;

	
	const datepickerProps = {
		showMonthDropdown: true,
		showYearDropdown: true,
		dropdownMode: 'select',
		minDate: new Date(),
		locale:'DPLocale'
	};

	const tooltipBorderColorSettings = [
		{
			value: tooltipBorderColor,
			onChange: colorValue => setAttributes({ tooltipBorderColor: colorValue }),
			label: __( "Tool Tip Border Color", 'wp-travel-blocks' )
		}
	];

	const tooltipBgColorSettings = [
		{
			value: tooltipBgColor,
			onChange: colorValue => setAttributes({ tooltipBgColor: colorValue }),
			label: __( "Tool Tip Background Color", 'wp-travel-blocks' )
		}
	];

	const btnBorderColorSettings = [
		{
			value: btnBordercolor,
			onChange: colorValue => setAttributes({ btnBordercolor: colorValue }),
			label: __( "Btn Border Color", 'wp-travel-blocks' )
		}
	];


	const btnTextColorSettings = [
		{
			value: btnTextColor,
			onChange: colorValue => setAttributes({ btnTextColor: colorValue }),
			label: __( "Btn Text Color", 'wp-travel-blocks' )
		}
	];

	const btnIconColorSettings = [
		{
			value: iconColor,
			onChange: colorValue => setAttributes({ iconColor: colorValue }),
			label: __( "Btn Icon Color", 'wp-travel-blocks' )
		}
	];

	const btnIconBackgroundColorSettings = [
		{
			value: btnIconBackgroundColor,
			onChange: colorValue => setAttributes({ btnIconBackgroundColor: colorValue }),
			label: __( "Btn Icon Background Color", 'wp-travel-blocks' )
		}
	];

	const toolTipColorSettings = [
		{
			value: tooltipColor,
			onChange: colorValue => setAttributes({ tooltipColor: colorValue }),
			label: __( "Tool Tip Color", 'wp-travel-blocks' )
		}
	];

	const btnBackgroundColorSettings = [
		{
			value: btnBackgroundColor,
			onChange: colorValue => setAttributes({ btnBackgroundColor: colorValue }),
			label: __( "Btn Background Color", 'wp-travel-blocks' )
		}
	];

	return (
		<div { ...useBlockProps() }>
			<Fragment>
			<BlockControls>
				<AlignmentControl
					value={textAlign}
					onChange={ ( nextAlign ) => {
						setAttributes( { textAlign: nextAlign } );
					} }
				/>
			</BlockControls>
			<InspectorControls>
				<PanelBody title={ __( 'Trip Calendar Options', 'wp-travel-blocks' ) } >
					<RangeControl
							label={__('Button Border Radius', 'wp-travel-blocks')}
							onChange={ (value) => setAttributes( { btnBorderRadius: value } ) }
							shiftStep={ 1 }
							min={ 2 }
							max={ 100 }
							value={ btnBorderRadius }
						/>
				</PanelBody>
				{ showTooltip && 
				<>
					<PanelColorSettings
						title={__('Tool Tip Text Color', 'wp-travel-blocks')}
						colorSettings={toolTipColorSettings}
						initialOpen={false}
					/>
					<PanelColorSettings
						title={__('Tool Tip Background Color', 'wp-travel-blocks')}
						colorSettings={tooltipBgColorSettings}
						initialOpen={false}
					/>
					<PanelColorSettings
						title={__('Tool Tip Border Color', 'wp-travel-blocks')}
						colorSettings={tooltipBorderColorSettings}
						initialOpen={false}
					/>
				</>
				 }
				
				<PanelColorSettings
					title={__('Button Background Color', 'wp-travel-blocks')}
					colorSettings={btnBackgroundColorSettings}
					initialOpen={false}
				/>
				<PanelColorSettings
					title={__('Button Icon Background Color', 'wp-travel-blocks')}
					colorSettings={btnIconBackgroundColorSettings}
					initialOpen={false}
				/>
				<PanelColorSettings
					title={__('Button Icon Color', 'wp-travel-blocks')}
					colorSettings={btnIconColorSettings}
					initialOpen={false}
				/>
				<PanelColorSettings
					title={__('Button Text Color', 'wp-travel-blocks')}
					colorSettings={btnTextColorSettings}
					initialOpen={false}
				/>
				<PanelColorSettings
					title={__('Button Border Color', 'wp-travel-blocks')}
					colorSettings={btnBorderColorSettings}
					initialOpen={false}
				/>
			</InspectorControls>
			</Fragment>
			<ServerSideRender
				block="wptravel/trip-calendar"
				attributes={ props.attributes }
			/>			
		</div>
	);
}
